<template>
  <div class="article-page">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" border="false">
      <!--  -->
      <template #header>
        <div class="header">
          <span> {{ total }} </span>
          <el-button
            @click="openDrawer('add')"
            icon="el-icon-plus"
            size="small"
            type="primary"
            round
          >
            
          </el-button>
        </div>
      </template>

      <!-- 
           el-table 
           el-table-column   lable   prop 

           el-card, el-table-column  element-ui 
          

          
       -->
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="stem" label="" width="400">
        </el-table-column>
        <el-table-column prop="creator" label=""> </el-table-column>
        <el-table-column prop="likeCount" label=""> </el-table-column>
        <el-table-column prop="views" label=""> </el-table-column>
        <el-table-column prop="createdAt" label="" width="200">
        </el-table-column>

        <!-- 
           <div v-for="(item,index) in data">
              <slot name="default" :row="item" :$index="index"></slot>
          </div>
       -->
        <el-table-column label="">
          <!-- obj => $index, row(item) -->
          <template #default="obj">
            <!-- {{obj.$index}}  {{obj.row.id}}-->
            <div class="actions">
              <i
                class="el-icon-view"
                @click="openDrawer('preview', obj.row.id)"
              >
              </i>
              <i
                class="el-icon-edit-outline"
                @click="openDrawer('edit', obj.row.id)"
              >
              </i>
              <i class="el-icon-delete" @click="del(obj.row.id)"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!--
      @size-change            10 => 20
      @current-change         ---
      :current-page=""   ,---
      :page-sizes="[100, 200, 300, 400]" 
      :page-size="100"        ---
      :total=""             ---
      layout                  
     -->
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="current"
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- drawer 
         title 
         :visible.sync  ,(.sync)
         direction="rtl"    
         :before-close    
    -->
    <el-drawer
      :title="drawerTitle"
      :visible.sync="isShowDrawer"
      direction="rtl"
      :before-close="handleClose"
      size="50%"
    >

      <div v-if="drawerType === 'preview'" class="article-preview">
  <h5>{{ form.stem }}</h5>
  <div v-html="form.content"></div>
</div>

      <el-form v-else ref="form" :model="form" :rules="rules" label-width="80px" >
        <el-form-item label="" prop="stem">
          <el-input v-model="form.stem" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- quill-editorelementelementblur
                    blurform
               -->
             <quill-editor v-model="form.content" @blur="handleBlur"></quill-editor>
           </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
  </div>
</template>

<script>
import { createArticle, getArticleDetail, getArticleList, removeArticle, updateArticle } from '@/api/article'
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'

export default {
  name: 'article-page',
  components: {
    quillEditor
  },
  data () {
    return {
      list: [],
      current: 1, // 
      pageSize: 10, // 
      total: 0, // 
      isShowDrawer: false, // 
      drawerType: '', //     'add' add preview edit
      form: {
        stem: '', // 
        content: '' // content
      },
      rules: {
        stem: [{ required: true, message: '', trigger: 'blur' }],
        content: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  async created () {
    this.initData()
  },
  methods: {
    //    
    async initData () {
      const { data } = await getArticleList({
        current: this.current,
        pageSize: this.pageSize
      })
      // console.log(data)
      this.list = data.rows
      this.total = data.total
    },
    async del (id) {
      // console.log(id)
      await removeArticle(id)
      this.$message.success('')
      this.initData() // 
    },
    handleCurrentChange (val) {
      // console.log(val)
      // 
      this.current = val
      // 
      this.initData()
    },
    async openDrawer (type, id) {
      // console.log(type,id)
      // type  add preview edit => () => 
      this.drawerType = type

      this.isShowDrawer = true // 

      //  
      if (type !== 'add') {
        const res = await getArticleDetail(id)
        this.form = {
          ...res.data
        }
      }
    },
    handleClose () {
      // console.log('')
      // 
      // , form
      this.form = { stem: '', content: '' }
      if (this.drawerType !== 'preview') {
        // add edit  resetFields : 
        this.$refs.form.resetFields()
      }
      this.isShowDrawer = false // 
    },
    handleBlur () {
      this.$refs.form.validateField('content')
    },
    async submit () {
      try {
        // 1.
        await this.$refs.form.validate()

        //    
        if (this.drawerType === 'add') {
          // 2.
          await createArticle(this.form)
          // 3.
          this.$message.success('')
        }
        if (this.drawerType === 'edit') {
          // 
          const { id, stem, content } = this.form
          await updateArticle({ id, stem, content })
          this.$message.success('')
        }
        // , , 
        this.current = 1
        // 
        this.initData()
        // 
        this.handleClose()
      } catch (e) {
        console.log(e)
      }
    }
  },
  computed: {
    drawerTitle () {
      let title = ''
      if (this.drawerType === 'add') title = ''
      if (this.drawerType === 'preview') title = ''
      if (this.drawerType === 'edit') title = ''

      return title
    }
  }
}
</script>

<style lang="scss" scoped>
.el-card {
  margin-top: 25px;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 16px;
  }
  .actions {
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    color: #666;
    > i:hover {
      color: rgba(114, 124, 245, 1);
      cursor: pointer;
    }
  }
}
.el-pagination {
  margin-top: 20px;
  text-align: center;
}
.el-breadcrumb {
  margin-top: 10px;
}
.el-form {
  padding-right: 40px;
}
.quill-editor {
  ::v-deep .ql-editor {
    height: 300px;
  }
}
.el-rate {
  padding: 10px 0;
}
.article-preview {
  padding: 0 40px 40px 40px;
  > h5 {
    font-size: 20px;
    color: #666;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 30px;
    margin: 0 0 20px 0;
  }
}
</style>
